import React, { useState, useEffect } from "react";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Table,
  Header,
  HeaderRow,
  HeaderCell,
  Body,
  Row,
  Cell,
} from "@table-library/react-table-library/table";
import { useTheme } from "@table-library/react-table-library/theme";

const THEME = {
  HeaderRow: `  
    font-weight: bold;
    font-size: 24px;
 
    background-color: #eaf5fd;
  `,
  Row: `
    font-size: 14px;
    
    &:hover {
      color: #212529;
      cursor: default;
    }
  `,
};

function Currency() {
  let [curr, setCurr] = useState([]);
  let [pic, setPic] = useState([]);
  const [search, setSearch] = useState("");
  const theme = useTheme(THEME);

  useEffect(() => {
    async function fetchData() {
      const result = await axios.get("/api/getCurrency");
      //"/api/getCurrency"
      //`https://my-own-cors-proxy-651654.herokuapp.com/https://poe.ninja/api/data/CurrencyOverview?league=Scourge&type=Currency`
      //`api/data/CurrencyOverview?league=Scourge&type=Currency`
      //console.log(result.data.lines[0]);
      console.log("Result from api:");
      console.log(result);
      setCurr(result.data.lines); //server
      //setCurr(result.data.lines); //dev
      let tmpArray = [];
      for (let item of result.data.lines) {
        for (let detail of result.data.currencyDetails) {
          if (item.currencyTypeName === detail.name) {
            //console.log(detail.name);
            tmpArray.push(detail.icon);
          }
        }
      }
      setPic([...tmpArray]);

      result.data.lines.forEach((arr) => {
        console.log("curr[i]:");
        console.log(arr.currencyTypeName);
      });

      //console.log("curr: " + curr);
      //console.log("tmpArray: " + tmpArray);
      let tmpJoined = [];
      console.log("length:");
      console.log(result.data.lines.length());
      for (let i = 0; i < result.data.lines.length(); i++) {
        console.log("curr[i]:");
        console.log(result.data.lines[i].currencyTypeName);
        console.log("tmpArray[i]");
        console.log(tmpArray[i]);
      }
      /*
      for (let item of result.data.lines) {
        console.log("Item: " + item.currencyTypeName);
        console.log("tmpArray[item]: " + tmpArray[item]);
        let obj = { Name: item, Image: tmpArray[item] };
        tmpJoined.push(obj);
      }
      console.log(tmpJoined);
      console.log("setPic");
      */
    }
    fetchData();
  }, []);
  /*
  let listItems = curr.map((cur, id) => (
    <tr key={id}>
      <td>
        <img src={pic[id]} alt={"Picture of " + cur.currencyTypeName}></img>{" "}
        {cur.currencyTypeName}
      </td>
      <td>{cur.chaosEquivalent}</td>
    </tr>
  ));
  */
  const data = {
    nodes: curr.filter((item) =>
      item.currencyTypeName.toLowerCase().includes(search.toLowerCase())
    ),
  };
  return (
    <>
      <label htmlFor="search">
        Search by Name:
        <input
          id="search"
          type="text"
          onChange={(event) => {
            setSearch(event.target.value);
          }}
        />
      </label>
      <Table data={data} theme={theme} layout={{ custom: true }}>
        {(tableList) => (
          <>
            <Header>
              <HeaderRow>
                <HeaderCell style={{ width: "100px" }}>Item</HeaderCell>
                <HeaderCell style={{ width: "100px" }}>Price</HeaderCell>
              </HeaderRow>
            </Header>
            <Body>
              {tableList.map((cur, id) => (
                <Row key={id} item={cur} style={{ width: "200px" }}>
                  <Cell style={{ width: "100px" }}>
                    {
                      <img
                        src={pic[id]}
                        alt={"Picture of " + cur.currencyTypeName}
                      ></img>
                    }{" "}
                    {cur.currencyTypeName}
                  </Cell>
                  <Cell style={{ width: "100px" }}>{cur.chaosEquivalent}</Cell>
                </Row>
              ))}
            </Body>
          </>
        )}
      </Table>
    </>
  );
}

export default Currency;
